<template>
  <div>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
    >
      {{ text }}
      <v-btn
        icon
        color="success"
        @click="snackbar = false"
      >
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  computed: {
    snackbar: {
      get() {
        return this.$store.getters.snackbar.visible;
      },
      set(value) {
        this.$store.commit('updatePropertyGlobal', { path: `snackbar.visible`, value })
      }
    },
    timeout: {
      get() {
        return this.$store.getters.snackbar.timeout;
      }
    },
    text: {
      get() {
        return this.$store.getters.snackbar.text;
      }
    }
  }
}
</script>

