import Vue from 'vue'
import Vuex from 'vuex'

// helper
import { recObjectByPath } from '@/helper/static'

// modules
import auth from './modules/auth'
import move from './modules/move'

import vuetify from '@/plugins/vuetify'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    snackbar: {
      visible: false,
      text: '',
      timeout: 2000
    },
    tips: {
      headers: [
        {
          text: vuetify.framework.lang.t('$vuetify.move.step5.tips.headers.colTip'),
          align: 'left',
          sortable: true, 
          filterable: true,
          value: 'tip'
        },
        { text: vuetify.framework.lang.t('$vuetify.move.step5.tips.headers.colMoment'), value: 'moment', sortable: true, filterable: true },
        { text: vuetify.framework.lang.t('$vuetify.move.step5.tips.headers.colLocation'), value: 'location', sortable: true, filterable: true }
      ],
      items: [{
        tip: 'Wohnung gründlich putzen',
        moment: 'Vor dem Umzug',
        location: 'Bisheriger Wohnort'
      }, {
        tip: 'Wohnungseinrichtung planen',
        moment: 'Vor dem Umzug',
        location: 'Neuer Wohnort'
      }, {
        tip: 'Neue Route zum Arbeitsort anschauen',
        moment: 'Vor dem Umzug',
        location: 'Unabhängig'
      }, {
        tip: 'An der Türe Notiz legen, dass Sie nun hier her kommen. Inkl. Datum, damit Nachbaren eventuell mit dem Lärm klar kommen',
        moment: 'Vor dem Umzug',
        location: 'Neuer Wohnort'
      }, {
        tip: 'Über Waschplan, etc. informieren',
        moment: 'Nach dem Umzug',
        location: 'Neuer Wohnort'
      }, {
        tip: 'Bisherige Gegenstände los werden, welche sowieso nicht mehr gebraucht werden - Neustart',
        moment: 'Vor dem Umzug',
        location: 'Unabhängig'
      }]
    }
  },
  getters: {
    tips: state => {
      return state.tips
    },
    snackbar: state => {
      return state.snackbar
    }
  },
  mutations: {
    updatePropertyGlobal: (state, { path, value }) => {
      recObjectByPath(state, path, value)
    },
    showSnackbar: (state, payload) => {
      const { text, timeout } = payload;

      state.snackbar = Object.assign({ visible: true, timeout: 5000 }, { text, timeout })
    }
  },
  modules: {
    auth,
    move
  }
})