import Vue from 'vue'
import Vuetify from 'vuetify/lib'

Vue.use(Vuetify)

// import languages (everything is with offical technical short https://www.w3.org/International/O-charset-lang.html)
import de from '@/i18n/de'
import en from '@/i18n/en'
import fr from '@/i18n/fr'
import it from '@/i18n/it'
import es from '@/i18n/es'
import pt from '@/i18n/pt'
import sq from '@/i18n/sq'

// handle languages
const fallbackLanguage = 'de' // if changed, do also make sure @/i18n is fixed (everything is enhancing german)

let lang
let language = navigator.language || fallbackLanguage

if (language.includes("-")) {
  lang = language.split("-")[0]
} else {
  lang = language
}

// supported languages -> Fallback is 'de'
if (
  lang !== 'de' || // german
  lang !== 'en' || // english
  lang !== 'fr' || // french
  lang !== 'it' || // italian
  lang !== 'es' || // spanish
  lang !== 'pt' || // portuguese
  lang !== 'sq' // albanian
) {
  lang = fallbackLanguage
}

export default new Vuetify({
  lang: {
    locales: { de, en, fr, it, es, pt, sq },
    current: lang,
  }
})